@import url("https://fonts.googleapis.com/css2?family=Bangers&display=swap");

html {
  height: 100%;
}
body {
  height: 100%;
  margin: 0;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.9);
}
/* navigation */
.nav {
  display: grid;
  grid-template-columns: 1fr 2fr;
  background-color: rgba(29, 118, 233, 0.9);
  color: #fff;
  justify-items: center;
  position: fixed;
  z-index: 10000;
  top: 0;
  width: 100%;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.nav-list {
  display: flex;
  flex-direction: row;
  padding: 0;
}
.nav-list li {
  list-style: none;
  margin: 5px 30px;
}
.nav-list li a {
  text-decoration: none;
  color: #fff;
  font-size: large;
  letter-spacing: 0.3px;
}
.nav-list li a.active {
  text-decoration: none;
  color: #fff;
  font-size: large;
  letter-spacing: 0.3px;
  border-bottom: 2px solid black;
}
/* container */
.container {
  display: grid;
  grid-template-columns: 1fr;
  width: 960px;
  margin: auto;
  justify-items: center;
  margin-top: 100px;
  text-align: center;
  background-color: #ffc324;
  font-family: "Bangers", cursive;
}
.container h1 {
  font-size: 3rem;
  color: black;
}
.container p {
  letter-spacing: 0.3px;
  font-size: 2rem;
  color: black;
}
.container:empty:after {
  content: "";
  height: 10px;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: linear-gradient(to left, #ffc324, #000);
  background-size: 200% 200%;
  animation: loadingGradient 1s infinite;
  z-index: 10011;
}
@keyframes loadingGradient {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: -200% 0%;
  }
}
footer {
  padding: 80px;
  position: relative;
  bottom: 0;
  color: whitesmoke;
  text-align: center;
}
footer a {
  text-decoration: none;
  color: rgba(29, 118, 233, 0.9);
  font-size: larger;
  text-align: center;
  margin: 0;
}
/* responsive */
@media (max-width: 700px) {
  .nav {
    grid-template-columns: 1fr;
    justify-items: center;
  }
  .container:empty:after {
    content: "";
    height: 10px;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: linear-gradient(to left, #ffc324, #000);
    background-size: 200% 200%;
    animation: loadingGradient 1s infinite;
    z-index: 10011;
  }
  div:nth-of-type(2) {
    width: 85%;
    background-color: #ffc324;
    margin-top: 150px;
    padding: 1rem;
  }
  .container {
    width: 85%;
    background-color: #ffc324;
    margin-top: 40px;
    padding: 1rem;
  }
  .container h1 {
    font-size: 2rem;
  }
  .container p {
    letter-spacing: 1.5px;
    font-size: 1rem;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
